<template>
	<view class="board">
		<view class="box">
			<view class="title">发布项目</view>
			<view class="handle_area flex flex_btween" style="align-items: start;">
				<view class="sub_box">
					<view class="line">
						<view class="tit">选择合同</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseContract">{{curContract.sign_template_name}}</view>
								<view class="work_wrap contract_wrap" v-if="showContract">
									<view class="item flex flex_btween" @click="dontNeedContract">不需要合同</view>
									<view class="item" v-for="(item,index) in list" :key="index"
										@click="chooseContract(item)">
										{{item.sign_template_name}}
									</view>
								</view>
							</view>
						</view>
					</view>
					<!-- <view class="line">
						<view class="tit">服务类别</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseType">{{workType.name}}</view>
								<view class="work_wrap" v-if="showWorkType">
									<workType @operation="getWorkType"></workType>
								</view>
							</view>
						</view>
					</view> -->
					<view class="line">
						<view class="tit">选择保险</view>
						<view class="input_wrap">
							<view class="work_type">
								<view class="work" @click="toChooseInsure">{{curInsure.meal_name}}</view>
								<view class="work_wrap contract_wrap" v-if="showInsure">
									<view class="item flex flex_btween" @click="dontNeed">不需要保险</view>
									<view class="item flex flex_btween" v-for="(item,index) in insureList" :key="index"
										@click="chooseInsure(item)">
										<view class="">{{item.meal_name}}</view>
										<view class="">{{item.cost_price + "/天；"+item.month_price + "/月"}}</view>
									</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">项目名称</view>
						<view class="input_wrap">
							<input type="text" v-model="projectName" placeholder="请输入项目名称" maxlength="32" />
						</view>
					</view>
					<view class="line">
						<view class="tit">用工方</view>
						<view class="input_wrap">
							<input type="text" v-model="name" placeholder="请输入用工方" />
						</view>
					</view>
					<view class="line">
						<view class="tit">是否显示用工方</view>
						<view class="input_wrap noborder">
							<radio-group @change="radioChange" class="flex flex-start">
								<label class="uni-list-cell uni-list-cell-pd flex flex-start"
									v-for="(item, index) in items" :key="item.value" style="margin-right: 15px;">
									<view>
										<radio :value="item.value" :checked="index === currentRadio" />
									</view>
									<view>{{item.name}}</view>
								</label>
							</radio-group>
						</view>
					</view>
					<view class="line">
						<view class="tit">用工人数</view>
						<view class="input_wrap">
							<input type="text" v-model="count" placeholder="请输入用工人数" />
						</view>
					</view>
					<view class="line">
						<view class="tit">项目地址</view>
						<view class="input_wrap">
							<input type="text" v-model="address" placeholder="请输入工作地址" />
						</view>
					</view>
				</view>
				<view class="sub_box">
					<view class="line">
						<view class="tit">服务时间</view>
						<view class="input_wrap flex flex_start noborder">
							<uni-datetime-picker type="datetime" :value="servStartTime" @change="chengeStartTime" />
							<view style="margin:0 20px;">—</view>
							<uni-datetime-picker type="datetime" :value="servEndTime" @change="chengeEndTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">签到时间</view>
						<view class="input_wrap noborder">
							<uni-datetime-picker type="datetime" :value="checkInTime" @change="chengeCheckInTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">到岗时间</view>
						<view class="input_wrap noborder">
							<uni-datetime-picker type="datetime" :value="arriveTime" @change="chengeArriveTime" />
						</view>
					</view>
					<view class="line">
						<view class="tit">服务费用</view>
						<view class="input_wrap flex flex-start">
							<input type="text" v-model="salary" placeholder="请输入薪资" />
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in periodList" :key="index"
									@click="choosePeriod(index)">
									<image
										:src="curWorkTimePeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">/</view>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">结算方式</view>
						<view class="input_wrap flex flex-start noborder">
							<!-- <input type="text" v-model="workTime" placeholder="请输入工作时长"/> -->
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in periodList" :key="index"
									@click="choosePeriod(index)">
									<image
										:src="curWorkTimePeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">发薪周期</view>
						<view class="input_wrap flex flex-start noborder">
							<!-- <input type="text" v-model="workTime" placeholder="请输入工作时长"/> -->
							<view class="periods flex flex-start">
								<view class="option flex flex-start" v-for="(item,index) in salaryPeriodList"
									:key="index" @click="chooseSalaryPeriod(index)">
									<image
										:src="curSalaryPeriodIndex == index?'/static/checked.png':'/static/uncheck.png'"
										mode="widthFix"></image>
									<view class="text">{{item.text}}</view>
								</view>
							</view>
							<view class="more flex flex_start">
								<view class="subt">发薪时间：</view>
								<picker mode="time" @change="changeSalayTime" v-if="curSalaryPeriodIndex == 0">
									<view>{{salaryTime}}</view>
								</picker>
								<uni-data-select v-model="weekValue" :localdata="weekRange" @change="weekChange"
									v-if="curSalaryPeriodIndex == 1"></uni-data-select>
								<view class="month flex flex-start" v-if="curSalaryPeriodIndex == 2">
									<input type="text" v-model="monthValue" />
									<text>号</text>
								</view>
							</view>
						</view>
					</view>
					<view class="line">
						<view class="tit">项目描述</view>
						<view class="input_wrap noborder" style="height:auto;">
							<textarea v-model="content" placeholder="请输入详细需求" auto-height="true" maxlength="500" />
						</view>
					</view>
					<view class="line">
						<view class="btn" @click="submit">提交</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	function getToday() {
		let date = new Date();
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
		if (month < 10) {
			month = "0" + month
		}
		if (day < 10) {
			day = "0" + day
		}
		return year + "-" + month + "-" + day
	}
	export default {
		name: "add_require",
		data() {
			return {
				projectName: "",
				name: "",
				servStartTime: getToday(),
				servEndTime: getToday(),
				checkInTime: getToday(),
				arriveTime: getToday(),
				status: "draft",
				content: "",
				count: "",
				workTime: "",
				salary: "",
				profession: "",
				location: "",
				figure: "",
				minAge: "",
				maxAge: "",
				minHeight: "",
				maxHeight: "",
				postName: "",
				workType: {
					name: "请选择"
				},
				curContract: {
					"sign_template_id": "",
					"sign_template_name": "请选择合同",
					"sign_template_status": "",
					"update_time": ""
				},
				endTime: getToday(),
				address: "",
				currentRadio: 1,
				is_employer_name_public: false,
				items: [{
						value: true,
						name: "展示"
					},
					{
						value: false,
						name: "隐藏"
					},
				],
				periodList: [{
						value: "hour",
						text: "时"
					},
					{
						value: "day",
						text: "日"
					},
					// {
					// 	value:"week",
					// 	text:"周"
					// },
					// {
					// 	value:"month",
					// 	text:"月"
					// },
				],
				salaryPeriodList: [{
						value: "day",
						text: "日"
					},
					{
						value: "week",
						text: "周"
					},
					{
						value: "month",
						text: "月"
					},
				],
				curWorkTimePeriodIndex: 0,
				curSalaryPeriodIndex: 0,
				work_duration_period: "",
				currentTabIndex: 0,
				salary_period: "",
				showWorkType: false,
				showContract: false,
				list: [],
				showInsure: false,
				curInsure: {
					"cost_price": "",
					"id": "",
					"meal_name": "请选择保险",
					"month_price": ""
				},
				insureList: [],
				salaryTime: "18:00",
				weekValue: 0,
				weekRange: [{
						value: 1,
						text: "星期一"
					},
					{
						value: 2,
						text: "星期二"
					},
					{
						value: 3,
						text: "星期三"
					},
					{
						value: 4,
						text: "星期四"
					},
					{
						value: 5,
						text: "星期五"
					},
					{
						value: 6,
						text: "星期六"
					},
					{
						value: 0,
						text: "星期日"
					}
				],
				monthValue: 15
			};
		},
		created() {
			// this.getInsureList()
			// this.getContractAuthStatus()
		},
		methods: {
			radioChange(e) {
				this.is_employer_name_public = e.detail.value
			},
			chengeStartTime(e) {
				this.servStartTime = e
			},
			chengeEndTime(e) {
				this.servEndTime = e
			},
			chengeCheckInTime(e) {
				this.checkInTime = e
			},
			chengeArriveTime(e) {
				this.arriveTime = e
			},
			choosePeriod(index) {
				this.curWorkTimePeriodIndex = index
			},
			chooseSalaryPeriod(index) {
				this.curSalaryPeriodIndex = index
			},
			changeTab(index) {
				this.currentTabIndex = index
			},
			changeSalayTime(e) {
				this.salaryTime = e.detail.value
			},
			weekChange(e) {
				console.log(e)
			},
			toChooseType() {
				this.showWorkType = !this.showWorkType
			},
			toChooseContract() {
				this.showContract = !this.showContract
				if (this.showContract) {
					this.getContractList()
				}
			},
			toChooseInsure() {
				this.showInsure = !this.showInsure

			},
			getWorkType(e) {
				this.showWorkType = false
				this.workType = e
				console.log(this.workType)
				this.getInsureList(e.type)

			},
			chooseContract(item) {
				// 判断合同是否可用
				let url = "/broker/contract/sign-template/" + item.sign_template_id + "/fields-check"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.showContract = false
						this.curContract = JSON.parse(JSON.stringify(item))
					}
				})

			},
			chooseInsure(item) {
				this.showInsure = false
				this.curInsure = JSON.parse(JSON.stringify(item))
				console.log((this.curInsure))
			},
			dontNeed() {
				this.curInsure = {
					"cost_price": "",
					"id": "",
					"meal_name": "不需要保险",
					"month_price": ""
				}
				this.showInsure = false
			},
			dontNeedContract() {
				this.curContract = {
					"sign_template_id": "",
					"sign_template_name": "不需要合同",
					"sign_template_status": "",
					"update_time": ""
				}
				this.showContract = false
			},
			getContractAuthStatus() {
				let _this = this
				let url = "/broker/contract/auth-status"
				this.$request(url).then(res => {
					if (res.code == 0) {
						if (res.data.fadada_auth && res.data.fadada_seal_free_sign) {
							// 调取模板列表
							this.getContractList()
						} else {
							uni.showModal({
								title: "提示",
								content: "您还没有完成法大大电子签章授权，请先授权后再发布项目",
								success(resp) {
									if (resp.confirm) {
										_this.setPageName({
											pageName: 'create_contract'
										})
									}
								}
							})
						}
					}
				})
			},
			getContractList() {
				let _this = this
				let url = "/broker/contract/sign-templates"
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						if (res.data.list.length == 0) {
							// 没有合同，先跳转我的模板，创建合同
							uni.showModal({
								title: "提示",
								content: "您还没有创建合同，是否去创建合同？",
								confirmText: "去创建",
								cancelText: "暂不需要",
								success(resp) {
									if (resp.confirm) {
										_this.setPageName({
											pageName: 'my_contracts'
										})
										_this.$emit("getPageName", {
											pageName: 'my_contracts'
										})
									}
								}
							})
						}
					}
				})
			},
			getInsureList(type) {
				let url = "/broker/insurance-meals?work_type=" + type
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.insureList = res.data.list
					}
				})
			},
			submit() {
				let data = {
					"name": this.projectName,
					"employer_name": this.name,
					"is_employer_name_public": this.is_employer_name_public,
					"work_address": this.address,
					"work_start_time": this.servStartTime.slice(0, 16),
					"work_end_time": this.servEndTime.slice(0, 16),
					"checkin_time": this.checkInTime.slice(0, 16),
					"arrival_time": this.arriveTime.slice(0, 16),
					"salary_unit_price": this.salary,
					"salary_settlement_type": this.periodList[this.curWorkTimePeriodIndex].value,
					"pay_salary_setting": {
						"daily": "",
						"weekly": "",
						"monthly": "",
						"type": ""
					},
					"work_type_id": this.workType.id,
					"need_worker_num": Number(this.count),
					"content": this.content,
					"contract_template_id": this.curContract.sign_template_id,
					"insurance_meal_id": this.curInsure.id
				}
				switch (this.curSalaryPeriodIndex) {
					case 0:
						data.pay_salary_setting.daily = String(this.salaryTime)
						data.pay_salary_setting.type = "daily"
						break;
					case 1:
						data.pay_salary_setting.weekly = String(this.weekValue)
						data.pay_salary_setting.type = "weekly"
						break;
					case 2:
						data.pay_salary_setting.monthly = String(this.monthValue)
						data.pay_salary_setting.type = "monthly"
						break;
					default:
						break;
				}
				console.log(data)
				this.$request("/broker/project", data, "POST").then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: "提交成功",
							duration: 2000
						})
						this.setPageName({
							pageName: "add_project_manager"
						}, res.data.id)
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	::v-deep {
		uni-picker {
			width: 100%;
			padding: 0 15px;
			height: 30px;
			line-height: 30px;
			border: 1px solid #e9e9e9;
			border-radius: 5px;
			margin-right: 20px;
		}

		.uni-select {
			height: 30px;
			font-size: 14px;
		}
	}

	.box {
		.sub_box {
			width: 48%;
		}

		.line {
			margin-bottom: 20px;

			&.scope {
				align-items: start;
			}

			.tit {
				color: rgba(0, 0, 0, 0.85);
				line-height: 22px;
			}

			.input_wrap {
				width: 460px;
				height: 30px;
				border-radius: 2px;
				border: 1px solid #D9D9D9;
				position: relative;
				margin-top: 8px;

				.more {
					.subt {
						white-space: nowrap;
						color: #f47106;
					}

					uni-picker {
						color: #f47106;
					}

					.month {
						input {
							width: 40px;
							height: 30px;
							text-align: center;
							border: 1px solid #ccc;
							padding: 0;
							border-radius: 4px;
							margin-right: 5px;
							color: #f47106;
						}
					}
				}

				&.age {
					input {
						border: 1px solid #e9e9e9;
						width: 140px;
						border-radius: 5px;
					}

					text {
						display: inline-block;
						margin: 0 10px;
					}
				}

				.work_type {
					position: relative;

					.work {
						height: 30px;
						line-height: 30px;
						padding-left: 15px;
						cursor: pointer;
						font-size: 14px;
					}

					.work_wrap {
						min-width: 100%;
						position: absolute;
						top: 100%;
						left: 0;
						z-index: 99;
						background: #fff;
						box-shadow: 0 0 5px 5px #e8e8e8;
						padding: 10px;
						box-sizing: border-box;
						height: 300px;
						overflow: scroll;
					}

					.contract_wrap {
						height: 110px;
						padding: 0;

						.item {
							font-size: 14px;
							height: 30px;
							line-height: 30px;
							padding: 0 10px;
							box-sizing: border-box;

							&:hover {
								cursor: pointer;
								background: #e8e8e8;
								color: $base-color;
							}
						}
					}
				}

				.abso {
					position: absolute;
					top: 50%;
					right: 15px;
					transform: translateY(-50%);
				}

				&.noborder {
					border: none;
				}

				input {
					width: 100%;
					height: 30px;
					padding-left: 15px;
					font-size: 14px;
				}

				.option {
					margin-right: 20px;
					cursor: pointer;

					image {
						width: 20px;
						height: 20px;
						margin-right: 5px;
					}
				}

				.long {
					image {
						width: 20px;
						height: 20px;
						margin-right: 5px;
						cursor: pointer;
					}
				}

				textarea {
					width: 100%;
					padding: 15px;
					min-height: 30px;
					max-height: 100px;
					border: 1px solid #D9D9D9;
					font-size: 14px;
					overflow-y: scroll;
				}
			}
		}
	}
</style>